const getURLSeparator = (url) => (url.includes('?') ? '&' : '?')

const getIntermediateURLWithTag = (url, intermediateURLConfig) => {
  if (!url || typeof url !== 'string') {
    return url
  }
  let finalURL = url
  const { intermediateURL, tag } = intermediateURLConfig
  if (tag) {
    finalURL += getURLSeparator(url)
    finalURL += `tag=${tag}`
  }
  if (intermediateURL) {
    finalURL = `${intermediateURL}${getURLSeparator(
      intermediateURL
    )}url=${encodeURIComponent(finalURL)}`
  }

  return finalURL
}

const getCardHTML = (data, config) => {
  return (productCardHTML = `<div class="js-card ProductCard_card ProductCardList_card ProductCard_web" >
        <a href="${getIntermediateURLWithTag(
          data.url,
          config.intermediateURLConfig
        )}" rel="noopener nofollow noreferrer sponsored" class="" target="_blank" >
          <div class="ProductCard_inner ProductCard_vertical_slim ProductCard_full_width" >
            <figure class="ProductCard_product_img">
              <div class="ProductCard_content">
                <img style="width:100%" src="data:image/gif;base64,R0lGODlhAQABAPAAAMzMzAAAACH5BAAAAAAALAAAAAABAAEAAAICRAEAOw==" data-symplor-src="${
                  data.imageURL
                }" class="ProductCard_contain" alt="${data.title}" title="${
    data.title
  }" />
              </div>
            </figure>
            <div class="ProductCard_product_details">
              <h5 class="ProductCard_title">${data.title}</h5>
              <div class="ProductCard_rating_with_logo">
                <span class="ProductCard_col">
                  <img style="height: 18px" src="data:image/gif;base64,R0lGODlhAQABAPAAAMzMzAAAACH5BAAAAAAALAAAAAABAAEAAAICRAEAOw==" data-symplor-src="https://static.symplor.com/images/affiliates/amazon-logo.png" class="ProductCard_logo ProductCard_logo_height" alt="Logo" title="Logo" />
                </span>
                ${
                  data.starRating
                    ? `<span class="ProductCard_text"><img title="star" src="data:image/gif;base64,R0lGODlhAQABAPAAAMzMzAAAACH5BAAAAAAALAAAAAABAAEAAAICRAEAOw==" data-symplor-src="https://static.symplor.com/images/affiliates/star.svg" alt="star">${data.starRating}</span>`
                    : ''
                }
              </div>
              <div class="ProductCard_price_cta_box">
                <div class="ProductCard_pricewrapper">
                  <div class="ProductCard_mrp">
                    <div class="ProductCard_original_price">
                      <span class="ProductCard_discount_price"
                        >${data.price.toLocaleString()}</span
                      >
                      <span class="ProductCard_mrp">${data.listPrice.toLocaleString()}</span>
                      <span class="ProductCard_new_offer">
                        <span class="ProductCard_offer_inner">${
                          data.discount
                        }<i>%</i></span
                        >Off</span
                      >
                    </div>
                  </div>
                  ${
                    data.offerText
                      ? `<div class="ProductCard_offer_text">${
                          data.offerText.split('|').length
                        }${config.offerText || '+ offers'}</div>`
                      : ''
                  } 
                </div>
                <div class="ProductCard_logo_cta_container">
                  <span></span>
                  <button type="button" class="ProductCard_cta">Buy Now</button>
                </div>
              </div>
            </div>
          </div>
        </a>
      </div>`)
}

const getHTML = ({ data, config = {} }) => {
  let productsHTML = ''
  if (Array.isArray(data)) {
    data.forEach((item) => {
      productsHTML += getCardHTML(item, config)
    })
  }
  return `<h3 class="s_w_title">${
    config.heading || `Today's Top Deals`
  }</h3><div class="ProductList_container ProductList_web">${productsHTML}</div>`
}

const enableSlider = () => {
  if (sliderJSPromise) {
    sliderJSPromise
      .then(() => {
        debugger
        tns({
          container: document.querySelector('.ProductList_container'),
          items: 2,
          slideBy: 'page',
          autoplay: true,
          gutter: 16,
          controls: false,
          autoplayHoverPause: true,
          autoplayButtonOutput: false,
          nav: false,
        })
      })
      .catch((err) => {
        console.log('Symplor Widgets :: failed to load slider script :: ' + err)
      })
  } else {
    console.log('Symplor Widgets :: failed to load slider script')
  }
}

const handleLazyImages = () => {
  if ('IntersectionObserver' in window) {
    inViewObserver = new IntersectionObserver(
      (entries) => {
        entries.forEach((entryToCheck) => {
          if (
            entryToCheck &&
            (entryToCheck.isIntersecting || entryToCheck.intersectionRatio > 0)
          ) {
            inViewObserver.unobserve(entryToCheck.target)
            entryToCheck.target.setAttribute(
              'src',
              entryToCheck.target.getAttribute('data-symplor-src')
            )
          }
        })
      },
      { rootMargin: '100px 100px 100px 100px' }
    )
    document.querySelectorAll('img[data-symplor-src]').forEach((img) => {
      inViewObserver.observe(img)
    })
  }
}

function sendGA(eventData = {}) {
  var name = eventData.name
  var params = eventData.params || {}
  if (typeof window !== undefined && typeof window.gtag === 'function') {
    window.gtag('event', name, {
      ...params,
      pp_url: window.top?.location?.href,
    })
  }
}

const bindGA = () => {
  productListEl.addEventListener('click', (event) => {
    const cardEl = event.target.closest('.js-card')
    if (cardEl) {
      try {
        sendGA({
          name: 'amazon_click',
          params: {
            action: 'Product_List_MREC',
            amazon_url: cardEl.querySelector('a').getAttribute('href'),
          },
        })
      } catch (err) {
        console.log('Symplor: error during ga call')
      }
    }
  })
}

const didMount = (el, props) => {
  // bindGA(el);
  handleLazyImages()
  if (props?.config?.layout === 'slider') {
    enableSlider()
  }
}

let sliderJSPromise = null

const loadSliderJS = () => {
  const scriptEL = document.createElement('script')
  const scriptSrc =
    'https://cdnjs.cloudflare.com/ajax/libs/tiny-slider/2.9.2/min/tiny-slider.js'
  scriptEL.src = scriptSrc
  scriptEL.id = scriptSrc

  sliderJSPromise = new Promise((resolve, reject) => {
    scriptEL.addEventListener('load', () => {
      setTimeout(() => {
        resolve()
      })
    })
    scriptEL.addEventListener('error', (e) => {
      reject(e)
    })
  })

  document.head.appendChild(scriptEL)
}

const ProductListMrec = (props) => {
  if (props?.config?.layout === 'slider') {
    loadSliderJS()
  }

  return {
    styles,
    html: getHTML(props),
    onLoad: (el) => {
      didMount(el, props)
    },
  }
}

window.symplor = window.symplor || { components: {} }
window.symplor.components.product_list_mrec = ProductListMrec
